.NotFound {
    display: flex;
    align-items: center;
    justify-content: center;
    height: calc(100vh - 100px);
  }
  
  .NotFound__content {
      align-items: center;
  }


  .NotFound__content h1 {
    font-size: 4rem;
    text-align: center;
}
  
  .NotFound__content p {
      font-size: 2rem;
      text-align: center;
  }

  @media all and (max-width: 1440px) {
    .NotFound__content h1 {
      font-size: 3rem;
    }
    .NotFound__content p {
      font-size: 1.5rem;
    }
  }
  
  